  box-sizing: border-box;
}

.onoffswitch-inner:before {
  content: "ON";
  padding-left: 10px;
  background-color: #1ab394;
  color: var(--bs-secondary-bg);
}

.onoffswitch-inner:after {
  content: "OFF";
  padding-right: 10px;
  background-color: var(--bs-secondary-bg);
  color: #999999;
  text-align: right;
}

.onoffswitch-switch {
