  fill: #A748CA;
}

.c3-tooltip {
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  opacity: 1;
}
.c3-tooltip td {
  border-left: none;
  font-family: "Roboto", serif;
}
.c3-tooltip td > span {
  background: #313a46;
}
.c3-tooltip tr {
  border: none !important;
}
.c3-tooltip th {
  background-color: #313a46;
  color: color-yiq(#313a46);
}

.c3-chart-arcs-title {
  font-size: 18px;
  font-weight: 600;
}

.c3 text {
  font-family: var(--bs-font-sans-serif);
  fill: var(--bs-body-color);
}

.c3-legend-item {
  font-family: "Roboto", serif;
  font-size: 14px;
}

.c3 line,
.c3 path {
  stroke: #ced4da;
}

.c3-chart-arc.c3-target g path {
  stroke: #fff;
}

/* CodeMirror */
.CodeMirror {
  border: 1px solid var(--bs-border-color);
  height: auto;
}

.CodeMirror-scroll {
  overflow-y: hidden;
  overflow-x: auto;
}

/* DATATABLES */
table.dataTable thead .sorting,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc,
table.dataTable thead .sorting_asc_disabled,
table.dataTable thead .sorting_desc_disabled {
  background: transparent;
}

.dataTables_wrapper {
  padding-bottom: 30px;
}

.dataTables_length {
  float: left;
}

.dataTables_filter label {
  margin-right: 5px;
}

.html5buttons {
  float: right;
}

.html5buttons a {
  border: 1px solid var(--bs-border-color);
  background: var(--bs-secondary-bg);
  color: #676a6c;
  box-shadow: none;
  padding: 6px 8px;
  font-size: 12px;
}
.html5buttons a:hover, .html5buttons a:focus:active {
  background-color: #eee;
  color: inherit;
  border-color: #d2d2d2;
}

div.dt-button-info {
  z-index: 100;
}

@media (max-width: 768px) {
  .html5buttons {
    float: none;
    margin-top: 10px;
  }
  .dataTables_length {
    float: none;
  }
}
.dt-bootstrap4 .form-control-sm {
  height: 29px !important;
  padding: 2px 10px;
}

.dt-buttons {
  margin-bottom: 12px !important;
}

.dt-search {
  float: inline-end;
  margin-bottom: 12px !important;
}

.dt-paging {
  margin-top: 12px !important;
  float: inline-end;
}

.dt-info {
  margin-top: 12px;
  display: inline-block;
}

.footable-odd {
  background-color: var(--bs-table-striped-bg);
}

/* Dropzone */
.dropzone {
  min-height: 140px;
  border: 1px dashed #1ab394;
  background: var(--bs-secondary-bg);
  padding: 20px 20px;
}
.dropzone .dz-message {
  font-size: 16px;
}

/* FLOT CHART  */
.flot-chart {
  display: block;
  height: 200px;
}

.widget .flot-chart.dashboard-chart {
  display: block;
  height: 120px;
  margin-top: 40px;
}

.flot-chart.dashboard-chart {
  display: block;
  height: 180px;
  margin-top: 40px;
}

.flot-chart-content {
  width: 100%;
  height: 100%;
}

.flot-chart-pie-content {
  width: 200px;
  height: 200px;
  margin: auto;
}

.flot-tick-label {
  fill: var(--bs-body-color) !important;
}

.jqstooltip {
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  visibility: hidden;
  background-color: #313a46 !important;
  color: var(--bs-white);
  border: none !important;
  text-align: left;
  white-space: nowrap;
  z-index: 10000;
  padding: 5px;
  border-radius: 3px;
}

.jqsfield {
  color: var(--bs-secondary-bg);
  font-family: "Roboto", serif !important;
  font-size: 14px !important;
}

